.popup__input {
  width: 100%;
  height: 47px;
  border: 0;
  border-bottom: 1px solid rgba(0,0,0,.2);
  font-size: 14px;
  line-height: 17px;
  box-sizing: border-box;
  padding: 5px 6px 13px;
  margin-bottom: 4px;
  outline: none;
}

.popup__input:focus {
  background: rgba(234, 243, 182, 0.171);
}

